"use client";

import axiosApi from "@/config/axios-api";
import { addToast } from "@heroui/toast";
import { createContext, useEffect, useState } from "react"

const HospitalContext = createContext()

function HospitalContextProvider({ children }) {

    const [ hnameStatus, setHnameStatus ] = useState(false); // เพิ่มสถานะการเช็คข้อมูลชื่อโรงพยาบาล
    const [ autoSaveMessage, setAutoSaveMessage ] = useState(null);
    const [hname, setHname] = useState("ชื่อโรงพยาบาล");
    const [hsname, setHsname] = useState("ชื่อโรงพยาบาล");
    const [hcode, setHcode] = useState("รหัสโรงพยาบาล");

    useEffect(() => {
        checkHname();
    }, []);

    const checkHname = async () => {
        try {
            const res = await axiosApi.get("/setting/hcode");
            if(res.status === 200 || res.status === 304){
                setHnameStatus(false)
                setHname(res.data.data[0]?.hcode_name || "ชื่อโรงพยาบาล");
                setHcode(res.data.data[0]?.hcode || "รหัสโรงพยาบาล");
                setHsname(res.data.data[0]?.hcode_name.replace(/โรงพยาบาล/g, "รพ."));
            }
        } catch (err) {
            console.log(err)
            if(err?.response?.status === 404 || err?.response?.data?.message === "Data not found!"){
                setHnameStatus(true);
                addToast({ title: "กรุณาตั้งค่าชื่อโรงพยาบาล", description: "คลิกที่ไอคอนรูปเฟืองมุมขวาบนเพื่อไปยังหน้าตั้งค่า", timeout: 8000, color: "danger", shouldShowTimeoutProgress: true });
            }
        }
    }

    const value = {  hnameStatus, setHnameStatus, hname, hsname, hcode, autoSaveMessage, setAutoSaveMessage };

    return <HospitalContext.Provider value={value}>{children}</HospitalContext.Provider>
}

export { HospitalContextProvider };
export default HospitalContext;
