import PopUpMessage from '@/components/modal/PopUpMessage';
import { Checkbox } from '@heroui/checkbox';
import axios from 'axios';
import Link from 'next/link';
import { createContext, useEffect, useState } from 'react'

const AppContext = createContext();

const APP_VERSION = process.env.NEXT_PUBLIC_APP_VERSION;

function formatNotice({ latest, published_at, url }) {

  const ts = published_at ? new Date(published_at).toLocaleString("th-TH", { dateStyle: "medium", timeStyle: "short" }) : "-";

  return [
    "ประกาศแจ้งเตือนการอัปเดตระบบ",
    "",
    `รุ่นที่ใช้งานปัจจุบัน\t: ${APP_VERSION}`,
    `รุ่นล่าสุดที่ประกาศ\t: ${latest}`,
    `วันที่ประกาศ\t: ${ts}น.`,
    "",
    "เพื่อความปลอดภัยและความถูกต้องของข้อมูล",
    "กรุณาติดต่อผู้ดูแลระบบของหน่วยงานท่านเพื่อดำเนินการอัปเดตเป็นรุ่นล่าสุดโดยเร็ว",
    "",
    url ? `รายละเอียดรุ่น (Release notes): ${url} ` : "",
    "",
    "จึงเรียนมาเพื่อโปรดทราบ"
  ].join("\n");
}

function AppContextProvider({ children }) {
    const [showModal, setShowModal] = useState(false)
    const [message, setMessage] = useState('');
    const [url, setUrl] = useState('');
    const [newVersion, setNewVersion] = useState('');

    useEffect(() => {
        const raw = localStorage.getItem('skipVersion');
        const saved = raw ? JSON.parse(raw) : null;

        fetchVersionOnGithub(saved);
    }, [])

    const headers = {
        Accept: "application/vnd.github+json",
        "X-GitHub-Api-Version": "2022-11-28",
    };

    const fetchVersionOnGithub = async (savedSkip) => {
        setShowModal(false);
        try {
            const res = await axios.get('https://api.github.com/repos/DoNuTll40/medical-record-audit/releases/latest', {
                headers,
            })

            if (res.status === 200) {
                let published_at = res.data.published_at;
                let latest = (res.data.tag_name || '').split('v')[1] || res.data.tag_name || '';
                let url = res.data.html_url;
                setUrl(url);
                setNewVersion(latest);
                setMessage(formatNotice({ latest, published_at, url }))

                const ignoredVersion = savedSkip?.version ?? savedSkip?.veriosn; // รองรับของเก่าที่สะกดผิด
                const isNewer = APP_VERSION !== latest; // โครงเดิมคุณใช้ !== ก็ปล่อยไว้
                const ignoredThis = ignoredVersion === latest && savedSkip?.skip === true;

                setShowModal(isNewer && !ignoredThis);
            }
        } catch (err) {
            console.log(err)
        }
    }

    const hdlCheckSkipVersion = (value) => {
        const skipVersion = {
            veriosn: newVersion,
            skip: value,
        }
        if (!value) {
            localStorage.removeItem('skipVersion');
        } else {
            localStorage.setItem('skipVersion', JSON.stringify(skipVersion));
        }
    }

    const value = {}

    return (
        <AppContext.Provider value={value}>
            {showModal &&
                <PopUpMessage 
                    title='เวอร์ชั่นใหม่พร้อมใช้งาน'
                    pre={message}
                    onClose={() => setShowModal(false)}
                    type='warning'
                    footer={
                        <div className="mt-2 mb-1 px-2 flex items-center justify-between">
                            <Checkbox
                                onValueChange={(value) => hdlCheckSkipVersion(value)}
                                type="checkbox"
                                size='sm'
                            >
                                ปิดการแจ้งเตือนเวอร์ชั่น
                            </Checkbox>
                            {url && (
                                <Link
                                    draggable="false"
                                    href={url}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    className="text-sm text-blue-600 hover:underline underline-offset-2"
                                >
                                    เปิด Release Notes
                                </Link>
                            )}
                        </div>
                    }
                />
            }
            {children}
        </AppContext.Provider>
    )
}

export {  AppContextProvider };
export default AppContext;