"use client";

import { ShieldAlert, CheckCircle2, Info, AlertTriangle, X } from "lucide-react";
import { motion, useDragControls } from "framer-motion";
import { ScrollShadow } from "@heroui/scroll-shadow";
import { useRef } from "react";
import Ripple from "@nuttawoot_donut/react-ripple";

export default function PopUpMessage({
  type = "error",
  title = "เกิดข้อผิดพลาด",
  message = "เกิดข้อผิดพลาด",
  pre = "",
  footer = "",
  onClose
}) {
  const ripple = new Ripple();

  const colors = {
    error: "bg-rose-500 dark:bg-rose-600 shadow shadow-rose-500/40 dark:shadow-rose-600/60 text-white",
    success: "bg-emerald-500 dark:bg-emerald-600 shadow shadow-emerald-500/40 dark:shadow-emerald-600/60 text-white",
    info: "bg-cyan-500 dark:bg-cyan-600 shadow shadow-cyan-500/40 dark:shadow-cyan-600/60 text-white",
    warning: "bg-amber-500 dark:bg-amber-600 shadow shadow-amber-500/40 dark:shadow-amber-600/60 text-white",
  };
  const icons = { error: <ShieldAlert size={18} />, success: <CheckCircle2 size={18} />, info: <Info size={18} />, warning: <AlertTriangle size={18} /> };

  // พื้นที่กรอบสำหรับจำกัดการลาก = เต็มหน้าจอ
  const boundsRef = useRef(null);

  return (
    <motion.div
      ref={boundsRef}
      key="popupmessage"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="fixed inset-0 flex items-center justify-center z-50 bg-black/30 dark:bg-white/5 backdrop-blur-xs px-4"
      aria-modal="true"
      role="dialog"
      tabIndex={-1}
    >
      <motion.div
        drag={false}
        dragConstraints={boundsRef}        // ลากได้ทั่วหน้าจอ
        dragElastic={0.15}
        dragMomentum={false}
        dragTransition={{ bounceStiffness: 600, bounceDamping: 20 }}
        initial={{ opacity: 0, scale: 0.5 }}
        animate={{ opacity: 1, scale: 1 }}
        className="max-w-md rounded-2xl w-full shadow-lg overflow-hidden will-change-transform"
        style={{ touchAction: "none" }}    // กันเบราเซอร์แย่ง gesture บนมือถือ
      >
        <div className="bg-white dark:bg-[#1f1f1f] rounded-2xl border border-black/10 dark:border-[#2C2C2C] px-2 pb-2">
          <div className="flex items-center justify-between py-2 select-none">
            <p className={`font-semibold text-sm rounded-full px-4 py-1.5 flex gap-1 items-center ${colors[type]} line-clamp-1`}>
              {icons[type]} {title}
            </p>

            <motion.button
              whileTap={{ scale: 0.98 }}
              onMouseUp={(e) => ripple.create(e)}
              onClick={onClose}
              className="ml-2 text-zinc-400 rounded-full p-1.5 hover:bg-zinc-100 dark:hover:bg-zinc-800 transition cursor-pointer"
              aria-label="ปิด"
            >
              <X size={16} />
            </motion.button>
          </div>

          <ScrollShadow className="pt-3 pb-3 px-5 max-h-[calc(100vh-40vh)] bg-zinc-100 rounded-2xl dark:bg-[#2c2c2c] overflow-y-auto">
            {pre ? (
              <pre className="text-sm text-zinc-600 dark:text-zinc-400 pr-2 whitespace-break-spaces">{pre}</pre>
            ) : (
              <p className="text-sm leading-relaxed">{message}</p>
            )}
          </ScrollShadow>
        {footer && (
          <>
            {footer}
          </>
        )}
        </div>
      </motion.div>
    </motion.div>
  );
}
